        <ul>
          <li>
            一、加强个人防护：
            <br />避免前往人群密集的公共场所。避免接触发热呼吸道感染病人，如需接触时要佩戴口罩。
            <br />2.勤洗手。尤其在手被呼吸道分泌物污染时、触摸过公共设施后、照顾发热呼吸道感染或呕吐腹泻病人后等。
            <br />3.不要随地吐痰。打喷嚏或咳嗽时用纸巾或袖肘遮住口、鼻。
            <br />4.加强锻炼，规律作息，保持室内空气流通。
          </li>

          <li>
            二、避免接触野生禽畜：
            <br />1.避免接触禽畜、野生动物及其排泄物和分泌物，避免购买活禽和野生动物。
            <br />2.避免前往动物农场和屠宰场、活禽动物交易市场或摊位、野生动物栖息地或等场所。
            <br />3.避免食用野生动物。不要食用已经患病的动物及其制品;要从正规渠道购买冰鲜禽肉，食用禽肉蛋奶时要充分煮熟。
          </li>

          <li>
            三、杜绝带病上班、聚会：
            <br />如有发烧、咳嗽等呼吸道感染的症状，居家休息，减少外出和旅行，天气良好时居室多通风，接触他人请佩戴口罩。要避免带病上班、上课及聚会。
          </li>

          <li>
