<script>
export default {
  data() {
    return {
      mark: 0,
      nowDate: "", // 当前日期
      nowTime: "", // 当前时间
      nowWeek: "", // 当前星期
      imgarr: [
        require("../../assets/img/疫情1.jpg"),
        require("../../assets/img/疫情3.jpg"),
        require("../../assets/img/疫情4.jpg"),
        require("../../assets/img/疫情5.jpg"),
        require("../../assets/img/疫情6.jpeg")
      ],

      drawer: false,
      direction: "rtl",
      msg: ["武", "汉", "加", "油"],
      show3: true,
      text: "武汉加油！"
    };
  },
  methods: {
    currentTime() {
      setInterval(this.getDate, 500);
    },
    getDate: function() {
      var _this = this;
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let week = new Date().getDay();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      if (week == 1) {
        this.nowWeek = "星期一";
      } else if (week == 2) {
        this.nowWeek = "星期二";
      } else if (week == 3) {
        this.nowWeek = "星期三";
      } else if (week == 4) {
        this.nowWeek = "星期四";
      } else if (week == 5) {
        this.nowWeek = "星期五";
      } else if (week == 6) {
        this.nowWeek = "星期六";
      } else {
        this.nowWeek = "星期日";
      }
      _this.nowTime = hh + ":" + mf;
      _this.nowDate = yy + "/" + mm + "/" + dd;
    },
    autoplay() {
      this.mark++;
      if (this.mark === 5) {
        this.mark = 0;
      }
    },
    timer() {
      setInterval(this.autoplay, 2000);
    },
    move(index) {
      this.mark = index;
    },

    // handleClose(done) {
    //     this.$confirm('确认关闭？')
    //       .then(_ => {
    //         done();
    //       })
    //       .catch(_ => {});
    //   },
    // 左侧加油栏
    textclick() {
      this.show3 = !this.show3;
      this.text = "抗击疫情，众志成城";
    }
  },
  created() {
    this.timer(), this.$refs.audio.play();
  },
  mounted() {
    document.body.style.backgroundColor = "#030303";
    // var _this = this; //声明一个变量指向Vue实例this，保证作用域一致
    // this.timer = setInterval(function() {
    //   _this.currentTime = //修改数据date
    //     new Date().getFullYear() +
    //     "-" +
    //     (new Date().getMonth() + 1) +
    //     "-" +
    //     new Date().getDate() +
    //     " " +
    //     new Date().getHours() +
    //     ":" +
    //     new Date().getMinutes() +
    //     ": " +
    //     new Date().getSeconds();
    // }, 1000);
    this.currentTime();
  }
  // beforeDestroy: function() {
  //     if (this.getDate) {
  //         console.log("销毁定时器")
  //         clearInterval(this.getDate); // 在Vue实例销毁前，清除时间定时器
  //     }
  // }
};
</script>

